.navbar {
	background: #fff;
	/* position: absolute; */
	padding: 5px 75px;
	/* top: 0; */
	width: 100%;
	z-index: 2;
	border-bottom: 0.05px solid #d0d0d0;
}
.navbar .one {
	order: 1;
	z-index: 9999;
}
.navbar .two {
	order: 2;
}
.navbar .three {
	order: 3;
}
.navbar .navbar-nav {
	gap: 16px;
}
.navbar a,
.navbar .nav-link {
	color: #000;
	font-size: 18px;
	font-weight: 500;
	padding: 12px 16px;
	line-height: normal;
}
.navbar a:hover {
	color: #666;
}
.navbar a.active {
	color: var(--primary-color) !important;
}
.navbar-collapse {
	justify-content: flex-end;
}
.navbar .cart-num {
	background-color: #b96464;
	color: #fff;
	position: absolute;
	border-radius: 50%;
	right: -12px;
	top: -12px;
	padding: 0 4px;
	font-size: 14px;
}
.navbar-collapse img {
	display: none;
}
@media only screen and (max-width: 500px) {
	.ar .navbar {
		direction: rtl;
	}
	.ar .navbar-collapse.show img {
		transform: rotate(180deg);
	}
	.navbar-collapse.show img {
		display: block;
	}
	.en .navbar-collapse.show {
		left: 0;
		opacity: 1;
	}
	.ar .navbar-collapse.show {
		right: 0;
		opacity: 1;
	}
	.navbar-nav .nav-link {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}
	.navbar {
		padding: 0.5rem 0;
	}
	.navbar .one {
		order: 1;
		z-index: 9999;
	}
	.navbar .two {
		order: 3;
	}
	.navbar .three {
		order: 2;
		z-index: 999;
	}
	.navbar-toggler {
		z-index: 999;
	}
	.navbar-collapse {
		position: fixed;
		background: #fff;
		top: 0px;
		z-index: 99;
		opacity: 0;
		padding-top: 81px;
		width: 60% !important;
		height: 100%;
		border-bottom: 1px solid #dedede;
	}
	.navbar .nav-link {
		color: #222 !important;
	}
	.navbar .navbar-nav {
		gap: 0;
	}
	.navbar-collapse a {
		margin-bottom: 0;
	}
	.navbar .logo img {
		width: 50px;
		height: 50px;
	}
}
