.categories {
	min-height: 100vh;
	margin-top: 90px;
	overflow: hidden;
}
.categories .products {
	margin-top: 0px;
	padding: 0;
}
.en .categories .products {
	/* padding-right: 75px; */
	padding-left: 0;
}
.ar .categories .products {
	/* padding-left: 75px; */
	padding-right: 0;
}
.categories hr {
	color: #d0d0d0;
}
.categories .type-title {
	font-size: 32px;
	line-height: 43.39px;
	font-weight: 800 !important;
	margin-bottom: 24px !important;
	margin-top: 0 !important;
}
.type-title span {
	font-size: 18px;
	font-weight: 400;
	line-height: 20.93px;
	color: #848484;
}

@media only screen and (max-width: 500px) {
	.categories {
		margin-top: 24px !important;
	}
	.categories .type-title {
		font-size: 24px;
	}
}
