.product-card {
	border: 1px solid #d0d0d0;
	border-radius: 10px;
	margin-bottom: 15px;
	transition: box-shadow 0.3s ease;
	height: 100%;
}
.product-card:hover {
	box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
}
.product-card .details {
	padding: 12px 7px;
}
.old-price {
	text-decoration: line-through;
}
.product-image {
	max-height: 250px;
	height: 250px;
	object-fit: cover;
	width: 100%;
	border-radius: 9px 9px 0 0;
}
.product-card .details {
	border-top: 1px solid #d0d0d0;
}
.product-card .details h6 {
	font-size: 18px;
	margin-bottom: 12px;
	font-weight: 500;
	cursor: pointer;
}
.product-card .details .prices p {
	margin: 0;
}
.product-card .details .prices {
	width: 90%;
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin-bottom: 12px;
	height: 35.5px;
}
.product-card .details .price {
	font-size: 16px;
	color: var(--primary-color);
	font-weight: 500;
}
.product-card .details .discount {
	font-size: 14px;
	background-color: #fef1e6;
	color: #055902;
	line-height: 19.23px;
	padding: 8px 12px;
	font-weight: 500;
	border-radius: 60px;
}
.product-card .details .brand {
	color: #848484;
	font-weight: 500;
	font-size: 14px;
	margin-bottom: 16px;
}
.product-card .details .brand span {
	color: #000;
}
.product-card .details .add-cart {
	background-color: var(--primary-color);
	border-radius: 10px;
	padding: 10px;
	color: #fff;
	font-weight: 500;
	width: 100%;
}
@media (max-width: 500px) {
	.product-image {
		max-height: 150px;
		height: 150px;
	}
	.product-card {
		overflow: hidden;
	}
	.product-card .details {
		height: calc(100% - 150px);
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	.product-card .details .prices {
		width: 100%;
		display: flex;
		justify-content: space-between;
		align-items: start;
		margin-bottom: 12px;
		height: auto;
		flex-direction: column;
	}
}
