.categories-home {
	margin-bottom: 40px;
	padding: 0 75px;
}
.categories-home .custom-category {
	background-color: var(--orange-color);
	border-radius: 20px;
	cursor: pointer;
}
.categories-home .custom-category .view-all {
	font-size: 18px;
	font-weight: 700;
	margin-bottom: 8px;
}
.category-card .arrowIcon {
	width: 32px;
	height: 32px;
}
.ar .category-card .arrowIcon {
	transform: rotate(180deg);
}
.category-card .information {
	display: block;
	background-color: var(--orange-color);
	text-align: center;
	padding: 10px;
	border-radius: 0 0 20px 20px;
}
.category-card {
	position: relative;
	height: auto;
	text-decoration: none;
	border-radius: 20px;
}
.category-card img {
	height: 300px;
	/* border-radius: 20px 20px 0 0; */
}
.category-card .title {
	font-size: 16px;
	margin-bottom: 8px;
	font-weight: 500;
	color: #fff;
	text-decoration: none;
	text-align: center;
}

