@media only screen and (max-width: 500px) {
	.categories-home {
		margin-top: 0 !important;
		margin-bottom: 2rem;
		padding: 0 16px;
	}
	.section-title {
		font-size: 24px !important;
		margin-top: 0 !important;
	}
	.category-card {
		text-align: center;
	}
	.category-card img,
	.categories-home .custom-category {
		height: 140px !important;
		width: 80% !important;
	}
	.categories-home .custom-category:last-of-type {
		width: 100% !important;
	}
}
